import getRegl from 'regl';
import { setC2 } from '@thi.ng/vectors/setc';
import { mulN2 } from '@thi.ng/vectors/muln';
import { divN2 } from '@thi.ng/vectors/divn';
import { invert2 } from '@thi.ng/vectors/invert';
import { map } from '@epok.tech/fn-lists/map';
import { each } from '@epok.tech/fn-lists/each';
import { wrap } from '@epok.tech/fn-lists/wrap';
import { range } from '@epok.tech/fn-lists/range';
import getFPS from 'fps-indicator';

import gpgpu from '@epok.tech/gl-gpgpu/src';
import { mapStep } from '@epok.tech/gl-gpgpu/src/maps';
import { getPass } from '@epok.tech/gl-gpgpu/src/step';

import {
    vertDef as screenVert,
    positionsDef,
    countDef as screenCount,
    preDef as gpgpuPre
  } from '@epok.tech/gl-gpgpu/src/const';

import {
    mpm, extensions, extensionsOptional, toLink, linkPast, linkFull, toFields
  } from '../src';

import gridFadeFrag from './grid-fade/index.frag.glsl';
import gridFadeToScreenFrag from './grid-fade-to-screen/index.frag.glsl';

import opticalFlowFrag from './optical-flow/index.frag.glsl';
import opticalFlowToGridFrag from './optical-flow-to-grid/index.frag.glsl';

const { min, max, sign } = Math;
const { isFinite } = Number;
const { createObjectURL } = URL;

const screenPositions = positionsDef();

/**
 * @see [glsl-aspect](https://github.com/keeffEoghan/glsl-aspect/blob/master/src/index.glsl)
 * @see [glsl-aspect/contain](https://github.com/keeffEoghan/glsl-aspect/blob/master/src/contain.glsl)
 */
export const aspect = (size, fit, invert, to = []) => {
  divN2(to, size, fit(...size));

  return ((invert)? invert2(to, to) : to);
};

export async function demo(to) {
  // Set up GL.
  const { $canvas, pixelRatio = devicePixelRatio } = to;

  const api = getRegl({
    canvas: $canvas, pixelRatio,
    extensions: extensions(), optionalExtensions: extensionsOptional(),
    ...to.api
  });

  const { command = api, frame, texture, buffer, framebuffer, clear } = api;

  mpm(api, to);

  const { grid, gridToScreen, pointsToScreen } = to;
  const { fields: { index: gIndex }, gpgpu: gGPGPU } = grid;
  const { pre: gPre, size: gSize, merge: gMerge, maps: gMaps } = gGPGPU;

  const demo = to.demo = { to, ...to.demo };

  /** Optical-flow interaction process. */

  const ofPre = 'opticalFlow_';
  const opticalFlowVert = screenVert.replaceAll(gpgpuPre, ofPre);

  const opticalFlow = demo.opticalFlow = {
    to,
    vert: opticalFlowVert,
    frag: opticalFlowFrag,
    count: screenCount,
    gap: 3, lambda: 1e-3, alpha: 1e2,
    flip: [-1, -1], aspect: [1, 1], size: [0, 0], offset: [0, 0, 1, 1],
    at: 0,

    on: true,
    ...demo.opticalFlow,

    media: { facingMode: 'user', video: true, ...demo.opticalFlow?.media },
    input: {
      data: document.createElement('video'),
      min: 'linear', mag: 'linear'
    },
    frames: map(() => texture(), range(2), 0),
    /** Overwrite blend, don't look up past values, just set new values. */
    blend: { enable: true, func: { src: 1, dst: 0 } },
    depth: { enable: false },

    framebuffer:
      framebuffer({ colorType: 'float', depth: false, stencil: false })
  };

  opticalFlow.pass = command(opticalFlow.pipeline = {
    vert: (c, p) => p.vert,
    frag: (c, p) => p.frag,

    attributes: { opticalFlow_position: buffer(screenPositions) },

    uniforms: {
      next: (c, { frames: fs, at }) => wrap(at, fs),
      past: (c, { frames: fs, at }) => wrap(at+1, fs),
      gap: (c, p) => p.gap,
      lambda: (c, p) => p.lambda,
      alpha: (c, p) => p.alpha,
      aspect: (c, p) => p.aspect,
      flip: (c, p) => p.flip,
      offset: (c, p) => p.offset
    },

    blend: {
      enable: (c, p) => p.blend.enable,

      func: {
        srcRGB: (c, { blend: { func: { src: o, srcRGB: s } } }) => s ?? o,
        srcAlpha: (c, { blend: { func: { src: o, srcAlpha: s } } }) => s ?? o,
        dstRGB: (c, { blend: { func: { dst: o, dstRGB: s } } }) => s ?? o,
        dstAlpha: (c, { blend: { func: { dst: o, dstAlpha: s } } }) => s ?? o
      }
    },

    depth: { enable: (c, p) => p.depth.enable },
    count: (c, p) => p.count,
    framebuffer: (c, p) => p.framebuffer
  });

  /** @todo Handle `merge.update` if `grid` has `merge`. */
  opticalFlow.step = (state = opticalFlow) => {
    const s = state.onStep?.(state) ?? state;
    const { on, pass, at, frames: fs, input: i } = s;

    if(!on) { return s; }

    fs[s.at = (at+1)%fs.length].subimage(i.data);
    pass(s);

    return s;
  };

  const opticalFlowToGrid = demo.opticalFlowToGrid = {
    to,
    vert: opticalFlowVert,
    frag: opticalFlowToGridFrag,
    count: screenCount,
    /** Keep this in a similar scale as the `rescaleMotion` of `grid`. */
    rescaleFlow: [5, 5],
    /** Additive blend, don't look up past values, just add any new values. */
    blend: { enable: true, func: { src: 1, dst: 1 } },
    depth: { enable: false },
    onStep: null,
    ...demo.opticalFlowToGrid
  };

  const ofgLinkG = toLink({
      ...linkPast,
      pre: gPre, steps: gSize.steps, merge: gMerge,
      macros: { samples: 0, taps: 0 },
      maps: mapStep({ ...gMaps, ...linkPast.maps, derives: null })
    },
    (c, p) => p.to.grid.gpgpu);

  opticalFlowToGrid.link = { grid: ofgLinkG };

  opticalFlowToGrid.pass = command(opticalFlowToGrid.pipeline = {
    vert: (c, p) => p.vert,

    frag: (c, { frag: f, link: l, to: { head: h } }) =>
      h.glsl(h)+l.grid.macros+f,

    attributes: { opticalFlow_position: buffer(screenPositions) },

    uniforms: {
      rescaleFlow: (c, p) => p.rescaleFlow,
      opticalFlow: (c, p) => p.to.demo.opticalFlow.framebuffer.color[0],
      aspectScreen: (c, p) => p.to.screen.aspect,
      dt: (c, p) => p.to.timer.dt,
      rescaleMotion: (c, p) => p.to.rescale.motion
    },

    blend: {
      enable: (c, p) => p.blend.enable,

      func: {
        srcRGB: (c, { blend: { func: { src: o, srcRGB: s } } }) => s ?? o,
        srcAlpha: (c, { blend: { func: { src: o, srcAlpha: s } } }) => s ?? o,
        dstRGB: (c, { blend: { func: { dst: o, dstRGB: s } } }) => s ?? o,
        dstAlpha: (c, { blend: { func: { dst: o, dstAlpha: s } } }) => s ?? o
      }
    },

    depth: { enable: (c, p) => p.depth.enable },
    count: (c, p) => p.count,
    framebuffer: (c, p) => getPass(p.to.grid.gpgpu)?.framebuffer
  });

  opticalFlowToGrid.step = (state = opticalFlowToGrid) => {
    const s = state.onStep?.(state) ?? state;
    const { pass, to: { demo, grid } } = s;

    if(!demo.opticalFlow.on) { return s; }

    const { gpgpu: gGPGPU, fields: gf } = grid;
    const { maps: gm, passNow: gp, stepBy: gStepBy } = gGPGPU;

    gStepBy(gGPGPU, -1);
    gGPGPU.passNow = gm.valueToPass[gf.index.motionFull];
    pass(s);
    gGPGPU.merge?.update?.(gGPGPU);
    gGPGPU.passNow = gp;
    gStepBy(gGPGPU, 1);
  };

  /** Set up the renders when the input video is ready. */
  const ofSetup = opticalFlow.setup = async () => {
    const { demo: { opticalFlow }, gridToPoints } = to;
    const { input, frames, aspect: ar, size, setup } = opticalFlow;
    const gpGPGPU = gridToPoints.gpgpu;
    const { onStep: gpOnStep } = gpGPGPU;
    const $video = input.data;

    await $video.play();

    const { videoWidth: w, videoHeight: h } = $video;

    aspect(setC2(size, w, h), min, true, ar);
    opticalFlow.framebuffer.resize(w, h);
    // Pixels units; divide `gap` by the video resolution.
    opticalFlow.gap /= max(w, h);
    // Fill the flow frames with the first frame.
    each((f) => f(input), frames);

    gpGPGPU.onStep = (gpGPGPU) => {
      const s = gpOnStep?.(gpGPGPU) ?? gpGPGPU;
      const { passAt, demo: { opticalFlow, opticalFlowToGrid } } = s.to;

      !passAt && opticalFlow.step(opticalFlow);
      opticalFlowToGrid.step(opticalFlowToGrid);

      return s;
    };

    opticalFlow.on = true;
    $video.removeEventListener('canplay', setup);
  };

  const ofStart = opticalFlow.start = async (state = opticalFlow) => {
    const { media: mq, setup, input: { data: $v } } = state;

    if(!mq.video) { return state; }

    try {
      const src = state.stream = await navigator.mediaDevices.getUserMedia(mq);

      $v.addEventListener('canplay', setup);
      (('srcObject' in $v)? $v.srcObject = src : $v.src = createObjectURL(src));
    }
    catch(e) { console.warn(e); }

    return state;
  };

  opticalFlow.stop = (state = opticalFlow) => {
    const tracks = state.input.data.srcObject?.getTracks?.();

    each((t) => t.stop(), tracks);
    state.on = false;

    return state;
  };

  /** Grid fade. */
  const gridFade = demo.gridFade = {
    to,
    frag: gridFadeFrag,
    fade: 2e-3,
    expose: 0,
    scale: 3e1,
    order: 'next',
    on: true,
    ...demo.gridFade
  };

  const gfPre = 'gridFade_';
  const gfSteps = 2;
  const gfMerge = false;
  const gfFields = gridFade.fields = toFields((new Map()).set('fade', 4));

  const gfDerives = gridFade.derives = [];
  const { index: gfIndex, values: gfValues, alias: gfAlias } = gfFields;
  const { fade: gfFade } = gfIndex;

  gfDerives[gfFade] = [gfFade];

  const gfMaps = { values: gfValues, alias: gfAlias, derives: gfDerives };

  const gfGPGPU = gridFade.gpgpu = gpgpu(api, {
    to,
    pre: gfPre,
    steps: gfSteps, stepMax: gfSteps,
    stepNow: 0, passNow: 0,
    shape: [...gSize.shape],
    maps: gfMaps,
    merge: gfMerge,
    macros: { vert: 0 },

    frag: (c, { to: { demo: { gridFade: { frag: f, type, quality } } } }) =>
      ((type)? `#define type ${type}\n\n` : '')+
      ((quality)? `#define quality ${quality}\n\n` : '')+
      f,

    type: 'uint8',
    min: 'linear', mag: 'linear',

    uniforms: {
      fade: (c, p) => p.to.demo.gridFade.fade,
      expose: (c, p) => p.to.demo.gridFade.expose,
      scale: (c, p) => p.to.demo.gridFade.scale,

      colorLo: (c, p) => p.to.gridToScreen.colorLo,
      dt: (c, p) => p.to.timer.dt,
      chaos: (c, p) => p.to.chaos
    }
  });

  gridFade.step = (state = gridFade) => {
    const { on, type, gpgpu } = state;

    if(!on) { return state; }

    const { step } = gpgpu;

    step(gpgpu);
    ((type === 1) || (type === 'typeGauss')) && step(gpgpu);

    return state;
  };

  /** Grid fade to screen. */
  const gridFadeToScreen = demo.gridFadeToScreen = {
    to,
    frag: gridFadeToScreenFrag,

    blend: { enable: true, func: { src: 1, dst: 'one minus src alpha' } },
    depth: { enable: false }
  };

  const gfsLinkGF = toLink({
      ...linkFull, pre: gfPre, steps: gfSteps, merge: gfMerge,
      macros: { output: 0 },
      maps: mapStep({ ...gfMaps, ...linkFull.maps, derives: [[gfFade]] })
    },
    (c, p) => p.to.demo.gridFade.gpgpu);

  gridFadeToScreen.link = { gridFade: gfsLinkGF };

  gridFadeToScreen.pass = command(gridFadeToScreen.pipeline = {
    vert: (c, p) => p.to.demo.gridFade.gpgpu.vert,

    frag: (c, { frag, link: { gridFade: lgf }, to: { head: h } }) =>
      h.glsl(h)+lgf.macros+frag,

    attributes: { gridFade_position: buffer(screenPositions) },
    uniforms: { ...gfsLinkGF.state.uniforms },

    blend: {
      enable: (c, p) => p.blend.enable,

      func: {
        srcRGB: (c, { blend: { func: { src: o, srcRGB: s } } }) => s ?? o,
        srcAlpha: (c, { blend: { func: { src: o, srcAlpha: s } } }) => s ?? o,
        dstRGB: (c, { blend: { func: { dst: o, dstRGB: s } } }) => s ?? o,
        dstAlpha: (c, { blend: { func: { dst: o, dstAlpha: s } } }) => s ?? o
      }
    },

    depth: { enable: (c, p) => p.depth.enable },
    count: (c, p) => p.to.demo.gridFade.gpgpu.count
  });

  gridFadeToScreen.draw = (state = gridFadeToScreen) => {
    const { pass, to } = state;

    to.demo.gridFade.on && pass(state);

    return state;
  };

  const { onDraw: gsOnDraw } = gridToScreen;
  const { onDraw: psOnDraw } = pointsToScreen;

  gridToScreen.onDraw = (gridToScreen) => {
    const s = gsOnDraw?.(gridToScreen) ?? gridToScreen;
    const { gridFade } = s.to.demo;
    const { on, order, step, gpgpu } = gridFade;

    (order === 'past') && step(gridFade);
    gridToScreen.framebuffer = ((on)? getPass(gpgpu)?.framebuffer : null);

    return s;
  };

  pointsToScreen.onDraw = (pointsToScreen) => {
    const s = psOnDraw?.(pointsToScreen) ?? pointsToScreen;
    const { gridFade, gridFadeToScreen } = s.to.demo;
    const { order, step } = gridFade;

    (order === 'next') && step(gridFade);
    gridFadeToScreen.draw(gridFadeToScreen);

    return s;
  };

  demo.view ??= [];

  /** Resize the canvas and any dependent properties. */
  const resize = demo.resize = () => {
    const { demo, $canvas, screen, grid } = to;
    const { gridFade, view: [vw = innerWidth, vh = innerHeight] } = demo;
    const { bounds, limit, detail, gpgpu: gGPGPU } = grid;
    const gShape = gGPGPU.size.shape;
    const { size: ss, aspect: ar } = screen;
    const [w, h] = mulN2(ss, setC2(ss, vw, vh), pixelRatio);

    $canvas.width = w;
    $canvas.height = h;

    /** Screen aspect ratio, `JS`; use `min` for contain, `max` for cover. */
    aspect(ss, max, false, ar);
    /** Grid bounds resize proportional to screen. */
    map((b, i) => (limit*sign(b))*wrap(i, ar), bounds, 0);
    /** Grid data resize proportional to screen. */
    gGPGPU.resize(mulN2(gShape, ar, detail));
    /** Grid fade resize matches screen. */
    gridFade.gpgpu.resize(ss);
    /** Screen aspect ratio, `GLSL`; use `min` for contain, `max` for cover. */
    aspect(ss, max, true, ar);
  };

  demo.fps ??= getFPS({ style: 'color: #fff; background-color: #0006;' });

  demo.start = async (state = demo) => {
    const { opticalFlow, resize, to: { timer, frame: frameMPM } } = state;

    opticalFlow.on && await ofStart();

    addEventListener('resize', resize);
    resize();

    const timestep = timer.step;

    demo.queued = ((!isFinite(timestep))? frame(frameMPM)
      : (function loop() {
          return demo.queued = setTimeout(() =>
              demo.queued = frame(() => loop(frameMPM().demo.queued.cancel())),
            timestep);
        })());

    return state;
  };

  return self.mpm = to;
}

export default demo;
