import { SYSTEM } from "@thi.ng/random/system";
import { defHofOp } from "./compile/emit.js";
const [randMinMax, randMinMax2, randMinMax3, randMinMax4] = defHofOp(
  SYSTEM,
  ([o, a, b]) => `${o}=rnd.minmax(${a},${b});`,
  "o,a,b,rnd=op",
  "o,a,b"
);
export {
  randMinMax,
  randMinMax2,
  randMinMax3,
  randMinMax4
};
