/**
 * @todo Notice that `grid.detail` and `gridToPoints.gpgpu.scale` seem linearly
 *   connected to affect performance, probably because `pointsToGrid` tends to
 *   overlap more and cause more blending when the ratio of points to cells is
 *   high...?
 *   In any case, a way to decouple this would be to have a separate set of
 *   tracer points for rendering only that just follow the grid forces, so there
 *   can be more rendered points for the number of material points.
 *
 * @todo Fix grid artefacts:
 * - One clearer cause is `gridToPoints.positionAdjust[1] = 1`;
 *   explore, isolate, identify.
 * - One possible lesser cause is `grid.limit = 1e8`; probably to do with
 *   points crossing cells coinciding with time-step, seems to be helped by
 *   tweaking `passes` higher or lower (higher increases accuracy, lower seems
 *   to let points jump cells).
 */

import demo from './demo';

import { reduce } from '@epok.tech/fn-lists/reduce';
import { range } from '@epok.tech/fn-lists/range';

const { max, log2 } = Math;
const { isFinite, isInteger } = Number;

const query = new URLSearchParams(location.search);
const test = query.get('test');

const $canvas = document.querySelector('canvas');

if(test === 'p2g') {
  const s = $canvas.style;

  s.width = s.height = '100%';
  s.border = '1px dashed red';
  s.margin = 'auto';
  s.imageRendering = 'pixelated';
}

(async () => {
  const mpm = self.mpm = (await demo((test === 'p2g')? {
        $canvas,
        pixelRatio: 1,
        api: {
          attributes: { antialias: false }
        },
        head: {
          test: 'test_'+test,
          kernelSize: 1
        },
        pointsToGrid: {
          // on: false,
          // quads: true,
          framebuffer: null
        },
        grid: {
          on: false,

          // detail: 2e2,
          // detail: 1e2,
          detail: 96,
          // detail: 4,

          limit: 1e8
          // limit: 1e5
          // limit: 96
          // limit: 8
          // limit: 1
        },
        gridToPoints: {
          on: false,
          // gpgpu: { scale: log2(96) }
          // gpgpu: { scale: log2(96/3) }
          // gpgpu: { scale: 4 }
          gpgpu: { scale: 5.1 }
        },
        gridToScreen: { on: false },
        pointsToScreen: { on: false },
        demo: {
          view: [96, 96],
          opticalFlow: { on: false },
          gridFade: { on: false }
        },
        passes: 1
      }
    : {
        $canvas,
        pixelRatio: max(devicePixelRatio, 1.5) || 1.5,
        // ...
        g: reduce((g, _, i) => {
            const v = parseFloat(query.get(`g[${i}]`), 10);

            isFinite(v) && (g[i] = v);

            return g;
          },
          range(3), []),

        pointsToGrid: {
          on: (query.get('pointsToGrid.on') !== 'false'),
          quads: true
        },
        grid: {
          on: (query.get('grid.on') !== 'false'),
          detail: parseFloat(query.get('grid.detail') || 1e2, 10),
          limit: parseFloat(query.get('grid.limit') || 1e9, 10)
        },
        gridToPoints: {
          on: (query.get('gridToPoints.on') !== 'false'),
          // motionAdjust: 0,
          positionAdjusts: [0, 0.1],
          gpgpu: {
            scale:
              parseFloat(query.get('gridToPoints.gpgpu.scale') || 9, 10),
            // steps: 3
          }
        },
        gridToScreen: {
          on: (query.get('gridToScreen.on') !== 'false'),
          colorHi: [0, 0, 0, 0.75],
          colorLo: [0, 0, 0, 0],
          angleHSLA: [0, 0.5, 0.5, 0.2]
        },
        pointsToScreen: {
          on: (query.get('pointsToScreen.on') !== 'false'),
          colorHi: [1, 0.53, 0.27, 0.47],
          colorLo: [0, 0, 0, 0],
          // form: 2
        },
        demo: {
          fps: (query.get('demo.fps') !== 'false') && undefined,
          opticalFlow: {
            on: (query.get('demo.opticalFlow.on') !== 'false'),
            media: (() => {
              const to = {};
              const vq = 'demo.opticalFlow.media.video';
              const v = query.get(vq);
              const vy = (v === 'true');

              if(vy || (v === 'false')) {
                to.video = vy;

                return to;
              }

              let size;
              const vw = parseFloat(query.get(vq+'.width'), 10);
              const vh = parseFloat(query.get(vq+'.height'), 10);

              isInteger(vw) && ((size ??= to.video = {}).width = vw);
              isInteger(vh) && ((size ??= to.video = {}).height = vh);

              return to;
            })(),
            rescaleFlow: [1, 5]
          },
          gridFade: {
            on: (query.get('demo.gridFade.on') !== 'false')
          }
        },
        passes: parseFloat(query.get('passes') || 2, 10)
      }));

  mpm.demo.start();

  (test === 'p2g') && $canvas.addEventListener('click', () => {
    const { pointsToGrid: pg, gridToScreen: gs } = mpm;

    pg.framebuffer = ((gs.on = !gs.on)? undefined : null);
    console.log('gridToScreen', gs.on);
  });

  return mpm;
})();
